<template>
  <div class="recharge-cards">
    <!-- 顶部统计卡片 -->
    <div class="stats-grid">
      <div class="stat-card">
        <div class="stat-icon">
          <i class="fas fa-ticket-alt"></i>
        </div>
        <div class="stat-content">
          <span class="stat-label">总卡密数</span>
          <span class="stat-value">{{ cards.length }}</span>
        </div>
      </div>
      
      <div class="stat-card">
        <div class="stat-icon">
          <i class="fas fa-check-circle"></i>
        </div>
        <div class="stat-content">
          <span class="stat-label">未使用</span>
          <span class="stat-value">{{ unusedCount }}</span>
        </div>
      </div>
      
      <div class="stat-card">
        <div class="stat-icon">
          <i class="fas fa-coins"></i>
        </div>
        <div class="stat-content">
          <span class="stat-label">总金额</span>
          <span class="stat-value">¥{{ formatAmount(totalAmount) }}</span>
        </div>
      </div>
    </div>

    <!-- 搜索和筛选工具栏 -->
    <div class="toolbar">
      <div class="toolbar-left">
        <div class="search-box">
          <i class="fas fa-search"></i>
          <input 
            v-model="searchQuery" 
            placeholder="搜索卡号..."
          >
        </div>
      </div>

      <div class="toolbar-right">
        <div class="filters">
          <select v-model="statusFilter">
            <option value="all">全部状态</option>
            <option value="unused">未使用</option>
            <option value="used">已使用</option>
          </select>

          <select v-model="sortBy">
            <option value="createdAt">创建时间</option>
            <option value="amount">金额</option>
            <option value="status">状态</option>
          </select>

          <button 
            class="sort-btn"
            @click="toggleSortOrder"
          >
            <i :class="sortOrder === 'asc' ? 'fas fa-sort-amount-up' : 'fas fa-sort-amount-down'"></i>
          </button>
        </div>

        <div class="actions">
          <button 
            class="action-btn danger"
            @click="confirmBatchDelete"
            :disabled="selectedCards.length === 0"
          >
            <i class="fas fa-trash-alt"></i>
            批量删除
          </button>
          
          <button 
            class="action-btn"
            @click="exportSelected"
            :disabled="selectedCards.length === 0"
          >
            <i class="fas fa-file-export"></i>
            导出选中
          </button>
        </div>
      </div>
    </div>

    <!-- 卡密列表 -->
    <div class="cards-list">
      <!-- 桌面端表格视图 -->
      <div v-if="!isMobile" class="table-wrapper">
        <table>
          <thead>
            <tr>
              <th style="width: 40px">
                <input 
                  type="checkbox" 
                  :checked="isAllSelected"
                  @change="toggleSelectAll"
                >
              </th>
              <th>卡号</th>
              <th style="width: 100px">金额</th>
              <th style="width: 100px">状态</th>
              <th style="width: 180px">生成时间</th>
              <th style="width: 180px">使用时间</th>
              <th style="width: 120px">使用者</th>
              <th style="width: 80px">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="card in filteredCards" :key="card.cardNo">
              <td>
                <input 
                  type="checkbox"
                  v-model="selectedCards"
                  :value="card.cardNo"
                  :disabled="card.status === 'used'"
                >
              </td>
              <td>
                <div class="card-no">
                  {{ card.cardNo }}
                  <button class="copy-btn" @click="copyCardNo(card.cardNo)">
                    <i class="fas fa-copy"></i>
                  </button>
                </div>
              </td>
              <td>¥{{ formatAmount(card.amount) }}</td>
              <td>
                <span :class="['status-badge', card.status === 'used' ? 'used' : 'unused']">
                  {{ card.status === 'used' ? '已使用' : '未使用' }}
                </span>
              </td>
              <td>{{ formatDate(card.createdAt) }}</td>
              <td>{{ card.usedAt ? formatDate(card.usedAt) : '-' }}</td>
              <td>{{ card.usedBy?.username || '-' }}</td>
              <td>
                <button 
                  class="delete-btn"
                  @click="confirmDelete(card)"
                  :disabled="card.status === 'used'"
                >
                  <i class="fas fa-trash-alt"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- 移动端卡片视图 -->
      <div v-else class="mobile-cards">
        <div 
          v-for="card in filteredCards" 
          :key="card.cardNo" 
          class="card-item"
        >
          <div class="card-header">
            <input 
              type="checkbox"
              v-model="selectedCards"
              :value="card.cardNo"
              :disabled="card.status === 'used'"
            >
            <span :class="['status-badge', card.status === 'used' ? 'used' : 'unused']">
              {{ card.status === 'used' ? '已使用' : '未使用' }}
            </span>
          </div>

          <div class="card-body">
            <div class="card-row">
              <span class="label">卡号:</span>
              <div class="card-no">
                {{ card.cardNo }}
                <button class="copy-btn" @click="copyCardNo(card.cardNo)">
                  <i class="fas fa-copy"></i>
                </button>
              </div>
            </div>

            <div class="card-row">
              <span class="label">金额:</span>
              <span class="value">¥{{ formatAmount(card.amount) }}</span>
            </div>

            <div class="card-row">
              <span class="label">生成时间:</span>
              <span class="value">{{ formatDate(card.createdAt) }}</span>
            </div>

            <div class="card-row" v-if="card.status === 'used'">
              <span class="label">使用时间:</span>
              <span class="value">{{ formatDate(card.usedAt) }}</span>
            </div>

            <div class="card-row" v-if="card.status === 'used'">
              <span class="label">使用者:</span>
              <span class="value">{{ card.usedBy?.username || '-' }}</span>
            </div>
          </div>

          <div class="card-footer" v-if="card.status !== 'used'">
            <button 
              class="delete-btn"
              @click="confirmDelete(card)"
            >
              <i class="fas fa-trash-alt"></i>
              删除
            </button>
          </div>
        </div>
      </div>

      <!-- 空状态提示 -->
      <div v-if="filteredCards.length === 0" class="empty-state">
        <i class="fas fa-ticket-alt"></i>
        <p>暂无卡密数据</p>
      </div>
    </div>

    <!-- 删除确认弹窗 -->
    <div v-if="showDeleteModal" class="modal-overlay" @click="closeDeleteModal">
      <div class="modal delete-modal" @click.stop>
        <div class="modal-header">
          <h3>{{ isBatchDelete ? '批量删除卡密' : '删除卡密' }}</h3>
          <button class="close-btn" @click="closeDeleteModal">
            <i class="fas fa-times"></i>
          </button>
        </div>
        
        <div class="modal-content">
          <p class="warning-text">
            <i class="fas fa-exclamation-triangle"></i>
            {{ isBatchDelete ? 
              `确定要删除选中的 ${selectedCards.length} 个未使用卡密吗？` : 
              '确定要删除这个未使用的卡密吗？'
            }}
          </p>
          <p class="sub-warning">此操作不可恢复</p>
        </div>

        <div class="modal-footer">
          <button class="cancel-btn" @click="closeDeleteModal">取消</button>
          <button 
            class="confirm-btn danger"
            @click="deleteCards"
            :disabled="deleteLoading"
          >
            <template v-if="!deleteLoading">
              <i class="fas fa-trash-alt"></i>
              确认删除
            </template>
            <template v-else>
              <i class="fas fa-spinner fa-spin"></i>
              删除中...
            </template>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RechargeCards',
  data() {
    return {
      cards: [],
      searchQuery: '',
      statusFilter: 'all',
      sortBy: 'createdAt',
      sortOrder: 'desc',
      selectedCards: [],
      showDeleteModal: false,
      cardToDelete: null,
      deleteLoading: false,
      isBatchDelete: false,
      isMobile: false
    }
  },
  computed: {
    // 统计数据
    unusedCount() {
      return this.cards.filter(card => card.status === 'unused').length;
    },
    totalAmount() {
      return this.cards.reduce((sum, card) => sum + card.amount, 0);
    },
    // 筛选和排序
    filteredCards() {
      let result = [...this.cards];
      
      // 搜索过滤
      if (this.searchQuery) {
        result = result.filter(card => 
          card.cardNo.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }
      
      // 状态过滤
      if (this.statusFilter !== 'all') {
        result = result.filter(card => card.status === this.statusFilter);
      }
      
      // 排序
      result.sort((a, b) => {
        let compareResult;
        switch (this.sortBy) {
          case 'amount':
            compareResult = a.amount - b.amount;
            break;
          case 'status':
            compareResult = a.status.localeCompare(b.status);
            break;
          default:
            compareResult = new Date(a.createdAt) - new Date(b.createdAt);
        }
        return this.sortOrder === 'asc' ? compareResult : -compareResult;
      });
      
      return result;
    },
    // 全选状态
    isAllSelected() {
      const selectableCards = this.filteredCards.filter(card => card.status === 'unused');
      return selectableCards.length > 0 && 
             selectableCards.every(card => this.selectedCards.includes(card.cardNo));
    }
  },
  methods: {
    async loadCards() {
      try {
        const response = await this.$api.get('/api/recharge-cards');
        this.cards = response.data;
      } catch (error) {
        this.$message.error('加载卡密列表失败');
      }
    },
    copyCardNo(cardNo) {
      navigator.clipboard.writeText(cardNo)
        .then(() => this.$message.success('卡号已复制'))
        .catch(() => this.$message.error('复制失败'));
    },
    confirmDelete(card) {
      if (card.status === 'used') return;
      this.cardToDelete = card;
      this.showDeleteModal = true;
    },
    async deleteCards() {
      if (this.deleteLoading) return;
      
      try {
        this.deleteLoading = true;
        const cardsToDelete = this.isBatchDelete ? 
          this.selectedCards : [this.cardToDelete.cardNo];
          
        await this.$api.post('/api/recharge-cards/delete', { 
          cardNos: cardsToDelete
        });
        
        this.$message.success(
          this.isBatchDelete ? '批量删除成功' : '删除卡密成功'
        );
        
        // 重新加载数据
        await this.loadCards();
        this.selectedCards = [];
        this.closeDeleteModal();
      } catch (error) {
        this.$message.error(error.response?.data?.message || '删除失败');
      } finally {
        this.deleteLoading = false;
      }
    },
    formatAmount(amount) {
      return parseFloat(amount).toFixed(2);
    },
    formatDate(date) {
      return new Date(date).toLocaleString();
    },
    closeDeleteModal() {
      this.showDeleteModal = false;
      this.cardToDelete = null;
    },
    toggleSelectAll() {
      if (this.isAllSelected) {
        this.selectedCards = [];
      } else {
        this.selectedCards = this.filteredCards
          .filter(card => card.status === 'unused')
          .map(card => card.cardNo);
      }
    },
    toggleSortOrder() {
      this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc';
    },
    confirmBatchDelete() {
      this.isBatchDelete = true;
      this.showDeleteModal = true;
    },
    exportSelected() {
      const selectedData = this.cards.filter(card => 
        this.selectedCards.includes(card.cardNo)
      );
      
      // 创建CSV内容
      const csvContent = [
        ['卡号', '金额', '状态', '生成时间'].join(','),
        ...selectedData.map(card => [
          card.cardNo,
          this.formatAmount(card.amount),
          card.status === 'unused' ? '未使用' : '已使用',
          new Date(card.createdAt).toLocaleString()
        ].join(','))
      ].join('\n');

      // 创建下载链接
      const blob = new Blob(['\ufeff' + csvContent], { 
        type: 'text/csv;charset=utf-8;' 
      });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.setAttribute('href', url);
      link.setAttribute(
        'download', 
        `选中卡密_${new Date().toLocaleDateString()}.csv`
      );
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    checkMobile() {
      this.isMobile = window.innerWidth <= 768;
    }
  },
  created() {
    this.loadCards();
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile);
  }
}
</script>

